body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* overflow: hidden; */
  background-color: rgb(66,53,57);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  border-radius: 50px;
}

/* Track */
::-webkit-scrollbar-track {
  /* background: #f1f1f1;  */
  background: rgba(235, 235, 235, 0.7)
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #423539; 
  /* border-radius: 50px; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #E8945F; 
}
.side-nav {
  height: 94vh;
  background-color: rgb(0, 0, 0);
  background-color: rgb(66, 53, 57, 0.2);
}

.side-nav a {
  color: white;
}

.side-nav-item {
  padding: 15px;
  padding-left: 20px;

  -webkit-transition: background-color 1s ease-out;
  transition: background-color 1s ease-out;

  -webkit-transition: padding-left 0.5s ease-out;
  transition: padding-left 0.5s ease-out;
}

.side-nav-item-active {
  padding: 15px;
  padding-left: 40px;
  background-color: #E8945F;

  -webkit-transition: background-color 1s ease-out;
  transition: background-color 1s ease-out;

  -webkit-transition: padding-left 0.3s ease-out;
  transition: padding-left 0.3s ease-out;
}

.side-nav-col-1 {
  padding-left: 0rem;
}

.side-nav-col-2 {
  padding-right: 0rem;
}

.top-nav-bar {
  height: 6vh;
  background-color: rgb(0,0,0);
  background-color: rgb(66, 53, 57, 0.7);
}

.nav-bar-brand {
  padding: 0 100px;
  font-size: 1.2rem;
  font-weight: 550;
}

.top-nav-bar .brand-1 {
  padding-left: 5rem;
  color: #E8945F;
}

.top-nav-bar .brand-2 {
  color: #ffffff;
  
  -webkit-transition: color 0.5s ease-out;
  transition: color 0.5s ease-out;
}
.top-nav-bar .brand-2:hover {
  color: #E8945F;
}
.card-view {
  width: 20rem;
  border: 0;

  color: white;
  background-color: rgb(0, 0, 0);
  background-color: rgb(66, 53, 57, 0.2);

  margin: 1.5rem auto;
  padding: 2rem 3rem;
}

.card-view button {
  border: 0;
  background-color: #E8945F;
  color: white;
}

.card-view button:hover {
  background-color: rgb(250, 115, 32);
  color: white;
}

.btn:not(:disabled):not(.disabled):active {
  background-color: rgb(250, 115, 32);
  color: white;
}


.card-view-large {
  width: 76vw;
}

.card-view-medium {
  width: 38vw;
}

.card-view-small {
  width: 25vw;
}

.bg-image {
  background-image: url(/react-weibo/static/media/bg.a5babdcd.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  filter: blur(35px);
  -webkit-filter: blur(35px);

  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  min-height: 100vh;
}

Layout {
  height: 100%;
}

.btn:focus, .btn:active {
  outline: none !important;
  box-shadow: none;
  -webkit-box-shadow: none;
}

